'use client';

import React, { useState, useEffect } from 'react';
import { Check, X, HelpCircle, TrendingUp } from 'lucide-react';

// ──────────────────────────────────────────────────────────────────────
// Fade-in animation (same as the proficiency page)
// ──────────────────────────────────────────────────────────────────────
const FadeInView = ({ children, className, delay = 0 }) => {
  const [isVisible, setIsVisible] = useState(false);
  useEffect(() => {
    const t = setTimeout(() => setIsVisible(true), delay);
    return () => clearTimeout(t);
  }, [delay]);

  return (
    <div
      className={`transition-all duration-700 ease-out ${
        isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-8'
      } ${className}`}
    >
      {children}
    </div>
  );
};

// ──────────────────────────────────────────────────────────────────────
// Main component
// ──────────────────────────────────────────────────────────────────────
export default function PricingPage() {
  const [period, setPeriod] = useState('monthly');
  const [currency, setCurrency] = useState('USD');

  // ── Plans ────────────────────────────────────────────────────────
  const plans = [
    {
      id: 'bronze',
      name: 'Bronze',
      price: { monthly: 32.45, sixMonths: 162.25 },
      icon: 'https://anglo-link.com/images/bronze_membership.png',
      popular: false,
    },
    {
      id: 'silver',
      name: 'Silver',
      price: { monthly: 69.99, sixMonths: 349.95 },
      icon: 'https://anglo-link.com/images/silver_membership.png',
      popular: true,
    },
    {
      id: 'gold',
      name: 'Gold',
      price: { monthly: 174.99, sixMonths: 874.95 },
      icon: 'https://anglo-link.com/images/gold_membership.png',
      popular: false,
    },
  ];

  // ── Features ─────────────────────────────────────────────────────
  const features = [
    {
      name: 'Personal study plan',
      all: true,
      tooltip:
        'A step-by-step guide showing your performance in each lesson & which lesson to study next',
    },
    {
      name: 'Video lessons (80+)',
      all: true,
      tooltip:
        'A wide range of video lessons on pronunciation, grammar, vocabulary, conversation, including listening & speaking exercises',
    },
    {
      name: 'Written material',
      all: true,
      tooltip:
        'Written version of the video lessons with new example sentences to widen your range of vocabulary and expressions',
    },
    {
      name: 'Written exercises',
      all: true,
      tooltip:
        'Hundreds of written exercises to assimilate the structures and the vocabulary in order to improve your writing skills',
    },
    {
      name: 'Oral exercises',
      all: true,
      tooltip:
        'Voice recording of lessons and exercises to listen to and repeat in order to improve your spoken fluency',
    },
    {
      name: 'Speech recognition',
      all: true,
      tooltip:
        'Voice recognition software so you can check the clarity of your own pronunciation',
    },
    {
      name: 'Progress tracking & feedback (tests)',
      all: true,
      tooltip:
        'End-of-lesson tests to track your progress and show you which sections of the lesson you must revise',
    },
    {
      name: 'WhatsApp Conversation Practice Group',
      all: true,
      tooltip: 'Connect with your peers for regular conversation practice',
    },
    {
      name: 'Exclusive video lessons (45)',
      bronze: false,
      silver: true,
      gold: true,
      tooltip:
        'Additional video lessons to review the more challenging grammar and pronunciation topics...',
    },
    {
      name: 'Offline study',
      bronze: false,
      silver: true,
      gold: true,
      tooltip:
        'Option to download video lessons, audio recordings, and pdfs for offline access',
    },
    {
      name: 'Initial level test',
      bronze: false,
      silver: false,
      gold: true,
      tooltip:
        'A 30-minute Zoom call with Minoo to discover your exact level...',
    },
  ];

  // ── Helpers ───────────────────────────────────────────────────────
  const getPrice = (plan) => {
    const amount = period === 'monthly' ? plan.price.monthly : plan.price.sixMonths;
    const symbol = currency === 'USD' ? '$' : currency === 'EUR' ? '€' : '£';
    return `${symbol}${amount.toFixed(2)}`;
  };

  const getPeriodText = () => (period === 'monthly' ? 'per month' : 'for 6 months');

  // ── Color palette (exact same as the proficiency page) ─────────────
  const colors = {
    primary: '#4169E1',   // Royal Blue
    secondary: '#7B68EE', // Medium Slate Blue
    accent: '#FFF7AE',    // Soft Pastel Yellow
    white: '#FFFFFF',
    dark: '#333333',
  };

  // ── Render ────────────────────────────────────────────────────────
  return (
    <>
      {/* ── Background ── */}
      <div className="fixed inset-0 bg-gradient-to-br from-white via-[#F8FAFF] to-[#F0F4FF] -z-10" />

      {/* ── Floating decorative blobs ── */}
      <div className="fixed inset-0 overflow-hidden pointer-events-none -z-5">
        <div
          className="absolute top-20 right-10 w-72 h-72 rounded-full blur-3xl opacity-20"
          style={{
            backgroundColor: colors.primary,
            animation: 'float 8s ease-in-out infinite',
          }}
        />
        <div
          className="absolute bottom-20 left-10 w-96 h-96 rounded-full blur-3xl opacity-20"
          style={{
            backgroundColor: colors.secondary,
            animation: 'float 10s ease-in-out infinite 1s',
          }}
        />
      </div>

      {/* ── Header ── */}
      <section className="relative pt-16 pb-8">
        <div className="max-w-6xl mx-auto px-6 lg:px-8">
          <FadeInView delay={0} className="text-center mb-12">
            <div className="inline-flex items-center gap-2 px-4 py-2 rounded-full bg-[#4169E1]/10 border border-[#4169E1]/20 mb-6">
              <div
                className="w-2 h-2 rounded-full"
                style={{ backgroundColor: colors.primary }}
              />
              <span
                className="text-sm font-semibold"
                style={{ color: colors.primary }}
              >
                Membership Plans
              </span>
            </div>
            <h1
              className="text-4xl md:text-5xl font-bold mb-4"
              style={{ color: colors.dark }}
            >
              Choose the Plan that{' '}
              <span
                className="bg-gradient-to-r bg-clip-text text-transparent"
                style={{
                  backgroundImage: `linear-gradient(to right, ${colors.primary}, ${colors.secondary})`,
                }}
              >
                Fits You Best
              </span>
            </h1>
            <p className="text-lg max-w-2xl mx-auto" style={{ color: '#666666' }}>
              All plans include core learning tools. Upgrade for exclusive
              content and offline access.
            </p>
          </FadeInView>
        </div>
      </section>

      {/* ── Controls (period + currency) ── */}
      <section className="relative py-8">
        <div className="max-w-6xl mx-auto px-6 lg:px-8">
          <FadeInView delay={100} className="flex flex-col sm:flex-row justify-center items-center gap-6">
            {/* Billing period */}
            <div className="flex bg-gray-100 p-1 rounded-lg">
              <button
                onClick={() => setPeriod('monthly')}
                className={`px-6 py-2 rounded-md text-sm font-semibold transition-all ${
                  period === 'monthly'
                    ? 'bg-gradient-to-r from-[#4169E1] to-[#7B68EE] text-white'
                    : 'text-gray-700 hover:bg-gray-200'
                }`}
              >
                Monthly
              </button>
              <button
                onClick={() => setPeriod('sixMonths')}
                className={`px-6 py-2 rounded-md text-sm font-semibold transition-all relative ${
                  period === 'sixMonths'
                    ? 'bg-gradient-to-r from-[#4169E1] to-[#7B68EE] text-white'
                    : 'text-gray-700 hover:bg-gray-200'
                }`}
              >
                6 Months
                {period === 'sixMonths' && (
                  <span className="absolute -top-2 -right-3 bg-green-500 text-white text-xs px-2 py-1 rounded-full font-bold">
                    1 month free
                  </span>
                )}
              </button>
            </div>

            {/* Currency */}
            <div className="flex bg-gray-100 p-1 rounded-lg">
              {['USD', 'EUR', 'GBP'].map((cur) => (
                <button
                  key={cur}
                  onClick={() => setCurrency(cur)}
                  className={`px-4 py-2 rounded-md text-sm font-medium transition-all ${
                    currency === cur
                      ? 'bg-gradient-to-r from-[#4169E1] to-[#7B68EE] text-white'
                      : 'text-gray-700 hover:bg-gray-200'
                  }`}
                >
                  {cur === 'USD' ? '$ USD' : cur === 'EUR' ? '€ EUR' : '£ GBP'}
                </button>
              ))}
            </div>
          </FadeInView>
        </div>
      </section>

      {/* ── Pricing Table ── */}
      <section className="relative py-12">
        <div className="max-w-6xl mx-auto px-6 lg:px-8">
          <FadeInView delay={200}>
            <div className="group rounded-2xl overflow-hidden shadow-lg hover:shadow-2xl transition-shadow duration-300 border border-[#E8E8E8]/50">
              <div className="overflow-x-auto">
                <table className="w-full min-w-[900px]">
                  {/* Header */}
                  <thead>
                    <tr
                      className="border-b"
                      style={{
                        background: `linear-gradient(to right, ${colors.primary}05, ${colors.secondary}05)`,
                      }}
                    >
                      <th
                        className="p-6 text-left text-sm font-semibold"
                        style={{ color: colors.dark }}
                      >
                        <div className="flex items-center gap-2">
                          <TrendingUp
                            className="w-4 h-4"
                            style={{ color: colors.primary }}
                          />
                          Features
                        </div>
                      </th>
                      {plans.map((plan) => (
                        <th
                          key={plan.id}
                          className="p-6 text-center"
                          style={{
                            backgroundColor: plan.popular ? `${colors.accent}40` : 'transparent',
                          }}
                        >
                          <img
                            src={plan.icon}
                            alt={plan.name}
                            className="w-24 h-24 mx-auto mb-3 object-contain"
                          />
                          {plan.popular && (
                            <span className="inline-block bg-orange-500 text-white text-xs font-bold px-3 py-1 rounded-full mb-2">
                              Most popular
                            </span>
                          )}
                          <div
                            className="text-3xl font-bold"
                            style={{ color: colors.dark }}
                          >
                            {getPrice(plan)}
                          </div>
                          <p className="text-sm mt-1" style={{ color: '#666666' }}>
                            {getPeriodText()}
                          </p>
                          <button
                            className="mt-4 w-full font-semibold py-2.5 rounded-md transition"
                            style={{
                              background: `linear-gradient(135deg, ${colors.primary}, ${colors.secondary})`,
                              color: colors.white,
                            }}
                            onMouseEnter={(e) =>
                              (e.currentTarget.style.boxShadow = `0 8px 16px ${colors.primary}40`)
                            }
                            onMouseLeave={(e) => (e.currentTarget.style.boxShadow = '')}
                          >
                            Select
                          </button>
                        </th>
                      ))}
                    </tr>
                  </thead>

                  {/* Body */}
                  <tbody>
                    {features.map((feature, idx) => (
                      <tr
                        key={idx}
                        className="border-b last:border-b-0 hover:bg-[#F8F8F8] transition"
                      >
                        <td className="p-4 font-medium flex items-center gap-2">
                          {feature.name}
                          {feature.tooltip && (
                            <div className="group relative inline-block">
                              <HelpCircle
                                className="w-4 h-4 cursor-help"
                                style={{ color: colors.primary }}
                              />
                              <div
                                className="absolute hidden group-hover:block z-20 p-3 rounded-lg text-xs max-w-xs -top-2 left-1/2 transform -translate-x-1/2 -translate-y-full whitespace-nowrap"
                                style={{
                                  backgroundColor: '#333333',
                                  color: '#fff',
                                }}
                              >
                                {feature.tooltip}
                                <div
                                  className="absolute top-full left-1/2 transform -translate-x-1/2 -mt-1 w-0 h-0 border-l-8 border-r-8 border-t-8 border-transparent"
                                  style={{ borderTopColor: '#333333' }}
                                />
                              </div>
                            </div>
                          )}
                        </td>
                        {plans.map((plan) => (
                          <td key={plan.id} className="p-4 text-center">
                            {feature.all || feature[plan.id] ? (
                              <Check
                                className="w-6 h-6 mx-auto"
                                style={{ color: '#10b981' }}
                              />
                            ) : (
                              <X
                                className="w-6 h-6 mx-auto"
                                style={{ color: '#ef4444' }}
                              />
                            )}
                          </td>
                        ))}
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>

              {/* Mobile hint */}
              <p className="text-center text-xs mt-4 md:hidden" style={{ color: '#666666' }}>
                Swipe left/right to see all plans
              </p>
            </div>
          </FadeInView>
        </div>
      </section>

      {/* ── CSS Animations ── */}
      <style jsx>{`
        @keyframes float {
          0%,
          100% {
            transform: translateY(0);
          }
          50% {
            transform: translateY(-30px);
          }
        }
        .scrollbar-hide::-webkit-scrollbar {
          display: none;
        }
        .scrollbar-hide {
          -ms-overflow-style: none;
          scrollbar-width: none;
        }
      `}</style>
    </>
  );
}